.modal {
  z-index: 1;
  width: 100vw;
  height: 100vh;
  background-color: #000c;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-content {
  width: 85%;
  height: 94%;
  background-color: #fff2fd;
  border: 1px solid #888;
  border-radius: 137px 0;
  margin: 10px auto;
  padding: 25px 15px;
}

.close {
  fill: #00f;
  width: 17px;
  height: 17px;
  float: right;
  margin-bottom: 33px;
  font-size: 28px;
  font-weight: bold;
}

.kvadrat {
  width: 233px;
  height: 416px;
  display: none;
}

.kolo {
  width: 102px;
  height: 102px;
  border-radius: 50%;
  margin: 0 auto;
  display: block;
}

.wrapper-content {
  column-gap: 30px;
  margin-top: 8px;
  display: flex;
}

.standart {
  margin-bottom: 20px;
}

.vip {
  margin-bottom: 10px;
}

.buy {
  width: 145px;
  height: 40px;
  color: #fff;
  background-color: #4c00fe;
  border-radius: 5px;
  margin: 0 auto;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  display: block;
}

.inf {
  width: 230px;
